	import common from "../../common/common.js"
	var serverUrl = common.serverUrl
	export default{
		data(){
			return{
				userInfo:{}
			}
		},
		onShow() {
			// var me = this
			var userInfo = uni.getStorageSync("globalUser")
			if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
				this.userIsLogin = true
				this.userInfo=userInfo
			}else{
				this.userIsLogin = false
			}
		},
		methods:{
			modifyBirthday(){
				uni.navigateTo({
					url:"../meBirthday/meBirthday"
				})
			},
			modifySex(){
				uni.navigateTo({
					url:"../sex/sex"
				})
			},
			modifyCity(){
				uni.navigateTo({
					url:"../meCity/meCity"
				})
			},
			modifyNickname(){
				uni.navigateTo({
					url:"../meNickname/meNickname"
				})
			},
			operator(){
				uni.showActionSheet({
					itemList:["查看我的头像","从相册中选择上传"],
					success: (res) => {
						var index = res.tapIndex
						if(index==0){
							var faceArr = []
							faceArr.push(this.userInfo.faceIcon)
							uni.previewImage({
								urls:faceArr,
								current:faceArr[0]
							})
						}else if(index==1){
							uni.chooseImage({
								count:1,
								sizeType:["compressed"],
								sourceType:["album","camera"],
								success: (res) => {
									//获得临时路径
								var tempFilePath = res.tempFilePaths[0]






								uni.navigateTo({
									url:"../faceCrop/faceCrop?tempFilePath="+tempFilePath
								})

								
								}
							})
						
						}
					}
				})
			},
			cleanStorage(){
				uni.clearStorageSync()
				uni.showToast({
					title:"成功清除缓存",
					mask:false,
					duration:1500
				})
			},
			logout(){
				var userInfo = this.getGlobalUser("globalUser")
				uni.request({
					url:serverUrl+'/users/logout?userId='+userInfo.userId,
					header:{
						"Authorization":"Bearer "+this.userInfo.token
					},
					method:'POST',
					// header:{
					// 	"Authorization":userInfo.token
					// },
					success:(res) => {
						if(res.data.code==0){
							uni.removeStorageSync("globalUser")
							uni.switchTab({
								url:"../me/me"
							})
						}
					}
				})
		}
	}
}
